.i {
  display: flex;
  height: 100vh;
  gap: 40px;
  align-items: center;
}

/* LEFT */
.i-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.i-left-wrapper {
  padding: 50px;
  height: 60%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 18px;
}

.i-intro {
  font-size: 30px;
  font-weight: 300;
  color: var(--text);
}

.i-name {
  font-size: 60px;
  color: var(--text);
}

.i-tagline {
  font-size: 18px;
  font-weight: 500;
  color: var(--muted);
}

.i-title {
  height: 50px;
  overflow: hidden;
}

.i-title-wrapper {
  height: 100%;
  animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
  25% {
    transform: translateY(-50px);
  }

  50% {
    transform: translateY(-100px);
  }

  75% {
    transform: translateY(-150px);
  }

  100% {
    transform: translateY(-200px);
  }
}

.i-title-item {
  height: 50px;
  font-size: 30px;
  font-weight: 700;
  color: #59b256;
  display: flex;
  align-items: center;
}

.i-desc {
  color: var(--muted);
  font-weight: 300;
  line-height: 1.7;
  max-width: 560px;
}

/* CTA */
.i-cta {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  margin-top: 6px;
}

.i-cta-primary,
.i-cta-secondary {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 10px 14px;
  border-radius: 10px;
  font-weight: 600;
  text-decoration: none;
  transition: transform 120ms ease, opacity 120ms ease;
}

.i-cta-primary {
  background: var(--button-bg);
  color: var(--button-text);
  border: 1px solid var(--button-border);
}

.i-cta-secondary {
  background: transparent;
  color: var(--text);
  border: 1px solid var(--border);
}

.i-cta-primary:hover,
.i-cta-secondary:hover {
  transform: translateY(-1px);
  opacity: 0.95;
}

.i-cta-primary:focus,
.i-cta-secondary:focus {
  outline: 2px solid var(--text);
  outline-offset: 3px;
}

/* Scroll icon */
.i-scroll {
  width: 30px;
  height: 30px;
  position: absolute;
  bottom: 5px;
  color: var(--text);
}

/* RIGHT */
.i-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.i-img {
  width: min(500px, 90%);
  height: auto;
  aspect-ratio: 4 / 5;
  object-fit: cover;
  border-radius: 18px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.08),
    0 4px 10px rgba(0, 0, 0, 0.04);
  position: relative;
}


/* MOBILE */
@media screen and (max-width: 480px) {
  .i {
    flex-direction: column;
    height: auto;
    padding-top: 40px;
    gap: 24px;
  }

  .i-left-wrapper {
    padding: 18px;
    align-items: center;
    text-align: center;
    height: auto;
  }

  .i-name {
    font-size: 46px;
  }

  .i-title-item {
    font-size: 24px;
    justify-content: center;
  }

  .i-desc {
    display: none;
  }

  .i-scroll {
    display: none;
  }

  .i-right {
    max-width: 100%;
  }

  .i-img {
    width: min(280px, 90%);
  }
}